import Vue from 'vue'
import Vuex from 'vuex'
import merge from 'lodash.merge'

Vue.use(Vuex)

const SELECTED_PROPERTY = '__selected__'

const INITIAL_STATE = {
  items: [],
  filteredItems: [],
  sorter: {
    id: null,
    comparator: null,
    onKeyChangeCallback() {},
  },
  filters: {},
  page: 1,
}

function doFilter(state) {
  // var filteredItems = state.items.slice(0)
  // Object.keys(state.filters).forEach(function(key) {
  //   filteredItems = filteredItems.filter(function(item) {
  //     return state.filters[key](item)
  //   })
  // })
  // TODO: test this line instead of the above
  var filteredItems = Object.keys(state.filters).reduce((fitems, key) => fitems.filter(item => state.filters[key](item)), state.items.slice(0))

  state.page = 1
  state.filteredItems = state.sorter.comparator ? filteredItems.sort(state.sorter.comparator) : filteredItems
}

function isItemSelected(item) {
  // Just for information, but keep this method here so the outside components don't need to know about it
  return item[SELECTED_PROPERTY] || false
}

function setState(state, payload) {
  state.items = payload
  state.filteredItems = state.items
  state.sorter = merge({}, INITIAL_STATE.sorter)
  state.filters = merge({}, INITIAL_STATE.filters)
  state.page = INITIAL_STATE.page
}


// * * * VUEX * * *

var state = merge({}, INITIAL_STATE)

var getters = {
  getItems: state => state.items,
  pageItems: state => ipp => {
    if (ipp) {
      var begin = (state.page - 1) * ipp
      var end = state.page * ipp
      return state.filteredItems.slice(begin, end)
    }
    // There are no pages, then show all items
    return state.filteredItems
  },
  totalRows: state => state.filteredItems.length,
  totalPages: state => ipp => Math.ceil(state.filteredItems.length / ipp),
  selectedItems: state => state.items.filter(isItemSelected),
  isItemSelected: state => isItemSelected,
  page: state => state.page,
}

var mutations = {
  removeItem(state, item) {
    let index = state.items.indexOf(item)
    if (index !== -1) {
      state.items.splice(index, 1)
    }
  },
  setInitialState(state, payload) {
    if (state.items === payload) {
      // Items are the same
      return
    }
    // Set new items and init sort, filter, etc.
    setState(state, payload)
  },
  setItems(state, payload) {
    // To check problem - only one store - not instances of it.
    console.log('Current state.items: ')
    console.log(state.items)
    console.log('Setting store with: ')
    console.log(payload)
    // If we send false payload the store should reset itself
    if (payload) {
      // Set new items but keep the sort, filters, etc.
      state.items = payload
      state.filteredItems = {}
      doFilter(state)
    } else {
      // Reset everything
      state.sorter.onKeyChangeCallback()
      setState(state, [])
    }
  },
  resetState(state) {
    // Reset everything
    state.sorter.onKeyChangeCallback()
    setState(state, [])
  },
  addFilter(state, payload) {
    state.filters[payload.key] = payload.comparator
    doFilter(state)
  },
  removeFilter(state, payload) {
    if (state.filters[payload]) {
      delete state.filters[payload]
      doFilter(state)
    }
  },
  removeAllFilters(state) {
    if (Object.keys(state.filters).length > 0) {
      state.filters = {}
      doFilter(state)
    }
  },
  setSorter(state, payload) {
    var sorter = state.sorter

    if (sorter.id === payload.sortId) { return }

    // The sort key is new

    // Reset the old key
    sorter.onKeyChangeCallback(payload.sortId)

    // Set new key
    sorter.id = payload.sortId
    sorter.comparator = payload.comparator
    sorter.onKeyChangeCallback = payload.onKeyChangeCallback
    state.filteredItems.sort(sorter.comparator)
  },
  toggleItemSelection(state, item) {
    Vue.set(item, SELECTED_PROPERTY, !item[SELECTED_PROPERTY])
  },
  setItemsSelection(state, payload) {
    payload.items.forEach(function(item) {
      Vue.set(item, SELECTED_PROPERTY, payload.areSelected)
    })
  },
  setPage(state, payload) {
    state.page = payload
  },
}


export default new Vuex.Store({
  state,
  getters,
  mutations,
})



